.modal {
    z-index: 9999;
    width: 100vw;
    height: calc(100vh - rem(78px));
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.9);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: rem(30px 10px);

    &__box {
        position: relative;
        background: #1B1B1B;
    }

    &__close {
        position: absolute;
        width: rem(38px);
        height: rem(38px);
        top: rem(10px);
        right: rem(10px);
        font-size: 0;
        background: url("../../../images/svg/modal-close.svg") no-repeat 50% 50% / rem(18px 18px);
        cursor: pointer;
        transition: .3s;
        z-index: 2;
    
        &:hover {
            opacity: .7;
        }
    }
}
